.container {
  padding: 0 20px;
  background: linear-gradient(#000 30%, rgba(0, 0, 0, 0) 100%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2; }
  @media (min-width: 1026px) {
    .container {
      padding: 0 60px; } }

.inner {
  position: relative;
  width: 100%;
  height: 100%;
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.banner {
  width: 300px;
  height: 54px;
  background-size: 100%; }
  @media (min-width: 600px) {
    .banner {
      width: 500px;
      height: 88px; } }

.nav {
  display: none;
  margin-top: 40px; }
  @media (min-width: 600px) {
    .nav {
      display: block; } }
  .nav li {
    font-size: 18px;
    display: inline-block; }
    .nav li:not(:last-child) {
      margin-right: 35px; }
    .nav li:hover a {
      color: #FF1815; }
    .nav li:hover .underline {
      width: 100%; }
    .nav li a {
      color: #fff; }
    .nav li .underline {
      width: 0;
      height: 2px;
      background: #FF1815;
      margin: 0 auto;
      transition: all 0.25s cubic-bezier(0.42, 0, 0.58, 1); }

.menu-container {
  margin-top: 30px; }
  @media (min-width: 600px) {
    .menu-container {
      display: none; } }
  .menu-container .button {
    width: 34px;
    height: 34px;
    display: flex;
    justify-content: center;
    background: #232323; }
  .menu-container ul.menu {
    margin-top: 30px;
    padding: 12px 10px;
    display: none;
    position: absolute;
    background: #232323;
    border: 1px solid #FF1815;
    left: 50%;
    transform: translateX(-50%); }
    .menu-container ul.menu.active {
      display: block; }
  .menu-container li {
    font-size: 20px;
    color: #fff; }
    .menu-container li:not(:last-child) {
      margin-bottom: 10px; }
    .menu-container li a {
      color: #fff; }
